.todoPageDiv {
    width: 100%;
    height: 85%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.todoPageTaskList {
  position: relative;
  top: 20px;
  height: 60%;
  width: 90%;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 10px;
  border-radius: 10px;
  box-shadow: inset 0px 5px 8px 3px rgb(0,0,0,0.4);
}



.todoPageTaskList::-webkit-scrollbar {
  width: 10px;
}

.todoPageTaskList::-webkit-scrollbar-track {
  background-color: #f1f1f1;
  border-radius: 5px;
}

.todoPageTaskList::-webkit-scrollbar-thumb {
  background-color: rgb(157, 172, 157);
  border-radius: 5px;
}

.todoPageTaskList::-webkit-scrollbar-thumb:hover {
  background-color: rgb(108, 121, 108);
}

.darkTodoPageTaskList {
  background-color: #474E68;
  /* box-shadow: inset 0px 5px 5px 3px #323345; */
}

.darkTodoPageTaskList::-webkit-scrollbar {
  width: 10px;
}

.darkTodoPageTaskList::-webkit-scrollbar-track {
  background-color: rgba(0,0,0,0);
  border-radius: 5px;
}

.darkTodoPageTaskList::-webkit-scrollbar-thumb {
  background-color: #343549;
  border-radius: 10px;
}

.darkTodoPageTaskList::-webkit-scrollbar-thumb:hover {
  background-color: rgb(108, 121, 108);
}

.addTaskDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}

.darkAddTaskDiv {
  background-color: #474E68;
}
