.progressPage {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.archiveTitle {
    position: relative;
    top: 5px;
    font-size: 30px;
    color: #33333396;
    text-align: center;
}

.darkArchiveTitle {
    color: rgb(202, 202, 202);
}

.tip {
    font-size: 12px;
}

.toggleButton {
    position: absolute;
    right: 10px;
    top: 10px;
}